// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import zh_CN from './assets/locale/zh_CN';
import VeeValidate, { Validator } from 'vee-validate';
import App from './app'
import store from './store'

// register filters globally
var filters = require('./filter')

Object.keys(filters).forEach(function (k) {
  Vue.filter(k, filters[k])
})

Vue.use(VueRouter)
Validator.addLocale(zh_CN);
Vue.use(VeeValidate, {
  locale: 'zh_CN',
  delay: 0
});
// 注册全局指令,spring Boot不支持webp
import VueWebp from './common/directives/v-webp/index.js'
Vue.use(VueWebp)

// 创建一个路由器实例
// 并且配置路由规则
const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      name: 'login',
      path: '/login',
      component: resolve => require(['./views/login.vue'], resolve)
    },
    {
      name: 'protocol',
      path: '/protocol',
      meta: {
        requireAuth: true
      },
      component: resolve => require(['./views/protocol.vue'], resolve)
    },
    {
      name: 'account',
      path: '/account',
      meta: {
        requireAuth: true
      },
      component: resolve => require(['./views/newAccount.vue'], resolve)
    },
    {
      name: 'list',
      path: '/',
      meta: {
        requireAuth: true,
        requireVerifyComplete: true
      },
      component: resolve => require(['./views/applist.vue'], resolve)
    },
    {
      name: 'appinfo',
      path: '/appinfo/:appid?',
      meta: {
        requireAuth: true,
        requireVerifyComplete: true        
      },
      component: resolve => require(['./views/appinfo.vue'], resolve)
    },
    {
      name: 'developer',
      path: '/developer',
      meta: {
        requireAuth: true,
        requireVerifyComplete: true      
      },
      component: resolve => require(['./views/developer.vue'], resolve)
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})

router.beforeEach((to, from, next) => {
  window.scrollTo(0,0)
  // 登录验证...
  if (to.meta.requireAuth) {
    if(util.getCookie('auth_login')) {
      // 跳转前还需要验证是否已经填写账户信息
      if(to.meta.requireVerifyComplete && !util.getCookie('account')) {
        next({
          path: '/account'
        })
      } else {
        next()
      }
    } else {
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      })
    }
  } else {
    next()
  }
})

router.afterEach((to, from, next) => {
  
})

/* eslint-disable no-new */
var app = new Vue({
  el: '#app',
  store,
  router,
  render: h => h(App)
});




